@import url("https://fonts.googleapis.com/css2?family=Montserrat:wght@400;700&display=swap");
* {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
}

html {
  font-family: "Montserrat", sans-serif;
}

body {
  background-color: #e0eafc;
}

img {
  width: 100%;
}

.container {
  max-width: 400px;
  margin: 0 auto;
}

.header {
  background-color: #252e37;
  color: #ffffff;
  padding: 80px 30px;
}
.header h1 {
  font-size: 80px;
  text-align: center;
}

.cover {
  transition: all 0.3s ease;
  background-color: #ffffff;
  margin: 16px 0;
  border-radius: 8px;
  padding: 4px 0;
  cursor: pointer;
}
.cover:hover {
  box-shadow: 0 1.6px 6.4px rgba(0, 0, 0, 0.3);
}
.cover h2,
.cover p {
  margin: 16px;
}

.overlay {
  align-items: center;
  background-color: rgba(0, 0, 0, 0.9);
  bottom: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
}
.overlay img {
  display: block;
  max-width: 50rem;
}

.footer {
  background-color: #222222;
  padding: 48px;
  color: #ffffff;
}
.footer div {
  float: right;
}
.footer a {
  transition: color 0.3s ease;
  color: #ffffff;
  text-decoration: none;
  margin: 10px;
}
.footer a:hover {
  color: #6495ed;
}

@media only screen and (width <= 576px) {
  .header h1 {
    font-size: 50px;
    text-align: center;
  }
  .container {
    max-width: 80%;
    margin: 0 auto;
  }
  .modal {
    padding-top: 170px;
  }
  .modal img {
    width: 90%;
  }
  .footer {
    display: flex;
    flex-direction: column;
    text-align: center;
  }
  .footer div {
    text-align: center;
    margin-bottom: 20px;
  }
  .footer p {
    font-size: 12px;
  }
}